* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	background-color: white;
}

li, a {
	font-family: 'Karla', sans-serif;
	font-size: 16px;
	color: #a0a0a0;
}

nav {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	/*padding: 30px 10%;*/
	padding: 30px 7%;
	padding-left: 8%;
	
	/*border-style: solid;*/
	/*border-color:red;*/
}

.nav-link {
	list-style: none;
}

.menu li {
	display: inline-block;
	padding: 0 20px
}


.nav-link {
	text-decoration: none;
}

.nav-link:hover {
	color: #ef9135;
}

.logo {
	margin-right: auto;
	font-size: 20px;
}

.underline-link {
	text-decoration: none;
	padding-bottom: 5px;
	border-bottom: 4px solid #ef9135;
}

 @media screen and (max-width: 500px) {   
	.menu li {
	display: block;
	padding: 0 20px
	}
 }

 @media screen and (max-width: 500px) {   
	.underline-link {
		text-decoration: none;
		border-bottom: none;
	}
 }

* {
	margin: 0;
	padding: 0;
}

.container {
	display: flex;
	width: 85%;
	padding: 2%;
	margin: 0 auto;
		/*border: 1px solid red;*/

}

.caption {
	position: absolute;
	background-color: transparent;
	padding-left: 5%;
	padding-top: 4%;
	width: 30%;
	font-family: 'Karla', sans-serif;
	font-size: 1.6vw;
	/*font-size: 1.5em;*/
	color: white;
	font-weight: 50;
	text-align: center;
}

.image {
	display: flex;
	justify-content: center;
	object-fit: cover;
	width: 100%;	
	height: 100vh;
	/*background: url("../images/bike-bg.jpg") no-repeat;*/
	/*background-size: contain;*/
	/*height: 750px;*/
	/*border: 1px solid black;*/
}

.bike-image {
	width: 100%;
}

@media screen and (max-width: 770px) {   
    .bike-image {
        content: url(/static/media/bike-bg-770.76d17032.png) !important;
    } 
     .caption {
    	font-size: 2vw;
    	width: 35%;
    }
}

 @media screen and (max-width: 500px) {   
    .bike-image {
        content: url(/static/media/bike-bg-400.66df611e.png) !important;
        padding-bottom:3%;
    } 

    .caption {
    	font-size: 3vw;
    	width: 30%;
    	padding-top: 6%;
    	padding-left: 7%;
    }
 }



