.project-container {
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: 85%;
	/*width: 85%;*/
	padding: 2%;
	margin: 0 auto;
	font-family: 'Karla', sans-serif;
	font-size: 1.6vw;
	color: #a0a0a0;
	text-align: center;
}

h3 {
	padding-bottom: 5%;
}

img {
	height: 100%;
    width: 50%;
}

.title1 {
  text-align: center;
  display: block;
  padding-bottom: 5%;
  font-size: 2vw;
}

.title2 {
  text-align: center;
  display: block;
  padding-bottom: 5%;
  font-size: 2vw;
}

.title3 {
  text-align: center;
  display: block;
  font-size: 2vw;
}


 @media screen and (max-width: 500px) {   
	img {
		height: 100%;
		width: 100%;
	}

	.title1 {
		font-size: 1em;
	}	
	.title2 {
		font-size: 1em;
	}
	.title3 {
		font-size: 1em;
	}
 }
.about-container {
	display: flex;
	width: 85%;
	padding: 2%;
	padding-top: 5%;
	margin: 0 auto;
	justify-content: center;
	/*justify-content: flex-end;*/
	font-family: 'Montserrat', sans-serif;
}

.description {
    margin-left: 1%;
	/*padding-top: 5%;*/
	/*width: 28%;*/
	width: 50%;
	text-align: left;
	color: #a0a0a0;
	font-family: 'Karla', sans-serif;
	/*margin-right: auto;
*/	/*border: 1px solid black;*/
}

.description h2 {
	color: #a0a0a0;
	padding-bottom: 2%;
	font-size: 1.6vw;
}

.description h3 {
	color: #a0a0a0;
	padding-bottom: 6%;
	font-size: 1.2vw;
}

.description p {

	font-weight: 300;
	line-height: 150%
}

.intro {
	font-weight: 400;
	padding-bottom: 5%;
	font-size: 1.1vw;
}

.body {
	padding-bottom: 5%;
	font-size: 1.1vw;
}

.email {
	font-size: 1.1vw;
}

.email a {
	color: #ef9135;
	font-size: 1.1vw;
}


.profile-container{
    margin-right: 1%;
	width: 30%;
	border-radius: 5%/10%; 
	/*padding-top: 2%;*/
	/*margin-right: auto;*/

}

.profile {
	width: 100%;	
	background: url(/static/media/profile.bec89e04.JPG) no-repeat;
	background-size: contain;
	height: 500px;
	/*height: 36vh;*/
	/*border: 1px solid black;*/
}

.button-container {
	margin-top: 5%;
}

.button-container a, button:hover {
	cursor: pointer;
	color: #ef9135;
}

.resume {
	font-size: 1.1vw;
	padding: 2%;
	border: 1px solid #ef9135;;
	color: #ef9135;
	/*border-radius: 5%/10%; */
}


 @media screen and (max-width: 500px) {   
	.about-container {
		display: flex;
		flex-direction: column;
		/*border: 1px solid black;*/
		align-items: center;
	}

	.profile-container {
	    margin-right: -1%; /*need*/
		width: 100%;
		margin-bottom: 5%;
		margin-top: -4%; /*quick fix*/
	}

	.profile {
		background: url(/static/media/profile.bec89e04.JPG) no-repeat;
		background-size: cover;
		background-position:center;
		/*height: 40vh;*/
		/*width: 60vw;*/

	}

	.description {
		text-align: center;
		width: auto;
	}

	.description h2 {
		padding-bottom: 2%;
		font-size: 3.6vw;
	}

	.description h3 {
		padding-bottom: 6%;
		font-size: 3.2vw;
	}

	.description p {
		font-weight: 300;
		line-height: 150%
	}

	.intro {
		font-weight: 400;
		padding-bottom: 5%;
		font-size: 3.1vw;
	}

	.body {
		padding-bottom: 5%;
		font-size: 3.1vw;
	}

	.email {
		font-size: 3.1vw;
	}

	.email a {
		font-size: 3.1vw;
	}

	.button-container {
		padding-bottom: 3%;
	}

	.resume {
		font-size: 3.1vw;
	}


}



.footer {
	display: flex;
	justify-content: center;
	margin-top: 2%;
	margin-bottom: 2%;
	/*border-top: 1px solid currentcolor;*/
	position: relative;
	bottom: 0;
	text-decoration: none;
	background-color: rgb(239,145,53);
	height: 2%;
}

.footer-menu li {
	text-decoration: none;
	display: inline-block;
	padding: 0 20px;
	background-color: transparent;
	font-size: 1vw;
}

.footer-menu li, a {
	text-decoration: none;
	background-color: transparent;
}

.footer-menu li, a:hover {
	color: #ef9135;
}

.img-container img{
	display: flex;
	width: 85%;
	padding: 2%;
	padding-top: 5%;
	margin: 0 auto;
	justify-content: center;
}
